import { Component, OnInit } from '@angular/core';
import {ActivatedRoute,ParamMap} from '@angular/router';
import {UpdateStudentService} from './update-student.service';
import {Location} from '@angular/common';
import { from } from 'rxjs';

@Component({
  selector: 'app-update-student',
  templateUrl: './update-student.component.html',
  styleUrls: ['./update-student.component.css'],
  providers:[UpdateStudentService]
})
export class UpdateStudentComponent implements OnInit {
  student:any;
  constructor(private route:ActivatedRoute, 
              private updateStudentService:UpdateStudentService,
              private location:Location          
    ) { }

  ngOnInit(): void {
    let id;
    this.route.paramMap.subscribe((params:ParamMap)=>{
      id=params.get('id');
    });
    
    this.updateStudentService.getStudentById(id)
      .then(student=>this.student=student)
      .catch(err=>console.log(err));
  }

  onUpdateStudent()
  {
    console.log(this.student);
    this.updateStudentService.updateStudent(this.student)
      .then(res=>{alert('Cập nhật thành công!');this.location.back()})
      .catch(err=>alert("Có lỗi xảy ra vui lòng thử lại"));
  }

}
