import { Component, OnInit } from '@angular/core';
import {StudentOfSubjectService} from './student-of-subject.service';
import {ActivatedRoute,ParamMap} from '@angular/router';

@Component({
  selector: 'app-student-of-subject',
  templateUrl: './student-of-subject.component.html',
  styleUrls: ['./student-of-subject.component.css'],
  providers:[StudentOfSubjectService]
})
export class StudentOfSubjectComponent implements OnInit {
  count=1;
  subject:any;
  constructor(private studentOfSubjectService:StudentOfSubjectService,
              private route:ActivatedRoute        
    ) {}

  ngOnInit(): void {
    let id;
    this.route.paramMap.subscribe((params:ParamMap)=>{
      id= params.get('id');
    });

    this.studentOfSubjectService.getStudentOfSubject(id)
      .then(subject=>this.subject=subject)
      .catch(err=>console.log(err))
  }
  
}
