import { Component, OnInit } from '@angular/core';
import {AddSubjectService} from './add-subject.service';
import {Location} from '@angular/common';
import { from } from 'rxjs';

@Component({
  selector: 'app-add-subject',
  templateUrl: './add-subject.component.html',
  styleUrls: ['./add-subject.component.css'],
  providers:[AddSubjectService]
})
export class AddSubjectComponent implements OnInit {

  constructor( private addSubjectService:AddSubjectService,
                private location:Location
    ) { }

  ngOnInit(): void {
  }

  onSubmit(value)
  {
    this.addSubjectService.sendSubject(value)
      .then(res=>{alert("Thêm thành công");this.location.back();})
      .catch(err=>alert("Có lỗi xảy ra vui lòng thử lại"));
  }

}
