import { NgModule } from '@angular/core';
import { Routes,RouterModule } from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule} from '@angular/forms';

import { HomeComponent } from './home/home.component';
import { ListStudentsComponent } from './list-students/list-students.component';
import { ListSubjectsComponent } from './list-subjects/list-subjects.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { StudentOfSubjectComponent } from './student-of-subject/student-of-subject.component';
import { SubjectOfStudentComponent } from './subject-of-student/subject-of-student.component';
import { AddStudentComponent } from './add-student/add-student.component';
import { UpdateStudentComponent } from './update-student/update-student.component';
import { CourseRegistrationComponent } from './course-registration/course-registration.component';
import { AddSubjectComponent } from './add-subject/add-subject.component';
import { UpdateSubjectComponent } from './update-subject/update-subject.component';

const routesConfig: Routes=[
    {path:'home',component:HomeComponent},
    {path:'list-students',component:ListStudentsComponent},
    {path:'list-subjects',component:ListSubjectsComponent},
    {path:'list-subjects/student-of-subject/:id',component:StudentOfSubjectComponent},
    {path:'list-students/subject-of-student/:id',component:SubjectOfStudentComponent},
    {path:'list-students/add-student',component:AddStudentComponent},
    {path:'list-students/update-student/:id',component:UpdateStudentComponent},
    {path:'list-students/course/:id',component:CourseRegistrationComponent},
    {path:'list-subjects/add-subject',component:AddSubjectComponent},
    {path:'list-subjects/update-subject/:id',component:UpdateSubjectComponent},
    {path:'',component:HomeComponent},
    {path:'**',component:PageNotFoundComponent}
];

@NgModule({
    imports:[RouterModule.forRoot(routesConfig),CommonModule,FormsModule],
    declarations:[
        HomeComponent,
        ListStudentsComponent,
        ListSubjectsComponent,
        PageNotFoundComponent,
        StudentOfSubjectComponent,
        SubjectOfStudentComponent,
        AddStudentComponent,
        UpdateStudentComponent,
        CourseRegistrationComponent,
        AddSubjectComponent,
        UpdateSubjectComponent,
    ],
    exports: [RouterModule] 

})

export class AppRoutingModule{}