import { Component, OnInit } from '@angular/core';
import {SubjectOfStudentService} from './subject-of-student.service';
import {ActivatedRoute,ParamMap} from '@angular/router';
@Component({
  selector: 'app-subject-of-student',
  templateUrl: './subject-of-student.component.html',
  styleUrls: ['./subject-of-student.component.css'],
  providers:[SubjectOfStudentService]
})
export class SubjectOfStudentComponent implements OnInit {

  student:any;
  constructor(private subjectOfStudentService:SubjectOfStudentService,
              private route:ActivatedRoute
    ) { }

  ngOnInit(): void {
    let id;
    this.route.paramMap.subscribe((params:ParamMap)=>{
      id=params.get('id');
    });
    this.subjectOfStudentService.getSubjectOfStudent(id)
    .then(student=>this.student=student)
    .catch(err=>console.log(err));
  }
  
}
