import { Component, OnInit } from '@angular/core';
import {AddStudentService} from './add-student.service';
import {Location} from '@angular/common';

@Component({
  selector: 'app-add-student',
  templateUrl: './add-student.component.html',
  styleUrls: ['./add-student.component.css'],
  providers:[AddStudentService]
})
export class AddStudentComponent implements OnInit {

  constructor(private addStudentService:AddStudentService, private location:Location) { }

  ngOnInit(): void {
  }
  onSubmit(formAddStudent)
  {
      this.addStudentService.sendStudent(formAddStudent.value)
        .then(res=>{alert("Thêm thành công");this.location.back()})
        .catch(err=>alert("Đã xảy ra lỗi vui lòng thử lại"));
  }
}
